.wcf-addons-template-edit-popup-area {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  display: flex;
  visibility: hidden;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  transform: scale(1.1);
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.8);

  &.open-popup{
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    .wcf-addons-body-overlay{
      opacity: 1;
      visibility: visible;
    }
  }
}

.wcf-addons-body-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
  opacity: 0;
  visibility: hidden;
}

.wcf-addons-template-edit-popup {
  position: relative;
  z-index: 9;
  width: 100%;
  max-width: 645px;
  border-radius: 5px;
  background-color: #FFFFFF;
}
.wcf-addons-template-edit-header {
  display: flex;
  justify-content: space-between;
  padding: 18px 50px;
  border-bottom: 1px solid #dfe7f1;
}
.wcf-addons-template-edit-setting-title{
  margin: 0;
}

.wcf-addons-template-edit-cross {
  cursor: pointer;
  color: #000;
  transition: 0.4s;
}

.wcf-addons-template-edit-body {
  padding: 15px 50px;
}
.wcf-addons-template-edit-field{
  display: grid;
  grid-template-columns: 1fr 2fr;
  margin-bottom: 20px;
  align-items: center;
  label{
    font-weight: 600;
  }

  .wcf-addons-template-edit-input{
    border: 1px solid #d3dbe7;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: #f8fbff ;
    font-size: 15px;
    color: #656565;
  }
  .select2-container--default .select2-selection--multiple{
    border: 1px solid #d3dbe7;
    padding: 8px 10px;
    border-radius: 5px;
    background-color: #f8fbff ;
    font-size: 15px;
    color: #656565;
  }

  &.hidden{
    display: none;
  }
}

.wcf-addons-template-edit-footer {
  padding: 15px 50px;

  .wcf-addons-template-button-group {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 20px;
  }
}


//template status
.wcf-tmp-status-switch .post-status{
  font-size: 14px;
  font-weight: 600;
}
